const condition_set = {};



condition_set.default_conditions = [
    {
        condition: (val) => {
            if (val.length >= 1) {
                return true;
            }

            return false;
        },
        name: "longerThanOneLetter",
        not_satisfied_text: "    ",
        satisfied_text: "!"
    },
    {
        condition: (val) => {
            if (val.length <= 20) {
                return true;
            }

            return false;
        },
        name: "shorterThanGivenLength",
        not_satisfied_text: "  "
    }
]

condition_set.attendee_conditions = [
    {
        condition: (val) => {
            let words = val.split(' ');
            if (words.length >= 1 && words[0] != '' && words[0] != '#') {
                return true;
            } 
            return false;
        },
        name: "longerThanOneWord",
        not_satisfied_text: "    ",
        satisfied_text: "!"
    }
]

condition_set.select_conditions = [
    {
        condition: (val) => {
            if (val == -1) {
                return false;
            } else {
                return true;
            }
        },
        name: "pickOne",
        not_satisfied_text: "   ",
        satisfied_text: "!"
    }
]

condition_set.unchangable_conditions = (type) => {
    return [{
        condition: (val) => {
            return true;
        },
        name: "unchangable",
        not_satisfied_text: "",
        satisfied_text: `${type}    `
    }]
}

condition_set.registered_id_conditions = [
    {
        condition: (val) => {
            if (val.length < 6) {
                return false;
            }
            return true;
        },
        name: "longerThanSixLetters",
        not_satisfied_text: "  6  ",
        satisfied_text: '!'
    }, {
        condition: (val) => {
            if (val.includes(' ')) {
                return false;
            }
            return true;
        },
        name: "noWhiteSpace",
        not_satisfied_text: "  ",
        satisfied_text: '!'
    }
]

export {condition_set};
